@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Russo One", sans-serif;
}
body {
}


.node {
  width: 25px;
  height: 25px;
  margin: 0px 2px;
  outline: 2px solid red;
  display: inline-block;
}

.node-finish {
  background-image: url(/static/media/map-marker-alt-solid.2b7c48a7.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  fill: red;
}

.node-start {
  background-image: url(/static/media/galactic-senate-brands.bb024501.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(166, 255, 0, 0.75);
    border-radius: 100%;
  }
  50% {
    background-color: rgba(255, 230, 6, 0.75);
  }
  75% {
    transform: scale(1.2);
    background-color: rgba(253, 103, 2, 0.705);
  }
  100% {
    transform: scale(1);
    background-color: rgba(255, 0, 0, 0.5);
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(166, 255, 0, 0.75);
    border-radius: 100%;
  }
  50% {
    background-color: rgba(255, 230, 6, 0.75);
  }
  75% {
    transform: scale(1.2);
    background-color: rgba(253, 103, 2, 0.705);
  }
  100% {
    transform: scale(1);
    background-color: rgba(255, 0, 0, 0.5);
  }
}

.node-wall {
  background-color: rgb(255, 102, 0);
}

.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 0, 0);
  }
  50% {
    transform: scale(1.2);
    background-color: rgb(255, 145, 0);
  }
  100% {
    transform: scale(1);
    background-color: rgb(238, 255, 7);
  }
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 0, 0);
  }
  50% {
    transform: scale(1.2);
    background-color: rgb(255, 145, 0);
  }
  100% {
    transform: scale(1);
    background-color: rgb(238, 255, 7);
  }
}

.Grid {
  margin: 20px 30px;
  padding: 8px 1.5px;
